import React, { Component } from 'react';
import {
  StyleSheet,
  Text,
  View,
  FlatList,
  TouchableOpacity,
  Button,
  ScrollView,
  SectionList,
  Picker
} from 'react-native';

import {withRouter} from 'react-router'
import queryString from 'query-string'

import tempevents from "../tempdata/TempDataFile.js"
import EventElement from "../components/EventElement.js"
import DetailsComponent from "../components/DetailsComponent.js"
import FeaturedEvents from "../components/FeaturedEvents.js"
import FilterButtons from "../components/FilterButtons.js"

export default class HomeScreen extends Component {
  constructor(props)
  {
    super(props)
    console.log(this.props)
    this.query = queryString.parse(this.props.location.search)
    this.state =
    {
      events: null,
      title: "Search Results:"
    }
  }

  conditionFetch(){

  }
  categoryFetch(){
      console.log("FETCHING")
      fetch("https://hangouter-backend.herokuapp.com/api/v1/events")
      .then(response => response.json())
      .then((responseJson)=> {
        var received_events = (responseJson)
        var filtered_events = received_events.filter((item) => (item.category === this.query.category))
        this.setState({
          events: filtered_events,
        })
      })
      .catch(error=>console.log(error)) //to catch the errors if any
  }
  fetchData(){
    console.log(this.query)
    if ('category' in this.query){
      this.setState({
      title: "Events from " + this.query.category + " category."
      })
      this.categoryFetch()
    }else if ('condition' in this.query){
      this.setState({
        title: "Dnes a Popularne som ešte nedokončil"
      })
    }else if ('searchterm' in this.query){
      this.setState({
        title: "Viem, že mám hľadať " + this.query.searchterm + ". Ale ešte to treba dokončiť"
      })
    }
  }

  componentDidMount(){
    this.fetchData()
  }

  onRefresh() {
   this.setState({ isFetching: true }, function() { this.fetchData() });
  }

  _flatListRenderer({item}){
    var Element = withRouter(({history}) => (
      <TouchableOpacity onPress={() => {history.push("/details/" + item._id, {item: item})}}>
          <EventElement active={true} item={item}/>
      </TouchableOpacity>))
    return ( <Element/> )
  }

  render() {
    console.log(this.query)
    return (
      <View style={styles.container}>
        <Text style={styles.title}> {this.state.title} </Text>
        <FlatList style={styles.flatliststyle}
        data={this.state.events}
        renderItem={this._flatListRenderer.bind(this)}
        keyExtractor={(item,index) => item._id}
        />
      </View>
    );
  }
}

const styles = StyleSheet.create({
  container:{
    flex: 1,
    justifyContent: 'center',
    alignItems: 'stretch',
    backgroundColor: '#FFF'
  },
  featured: {
    height: 200,
  },
  title:{
    color: '#777',
    fontSize: 20,
    textAlign: 'center',
    marginVertical: 15,
  },
  flatliststyle:{
    flex: 5,
  },
});
